.footer--light {
    background-color: #1A1818;
    display: flex;
    flex-direction: row;
    height: 300px;;
  }
  
  .box1-footer {
    flex-direction: column;
  
  }

  @media (max-width: 991px) {
    .footer--light {
      flex-direction: column;
      height: 100%;
    }
  }
  
  #logo-footer {
    height: 80px;
    margin-top: 40px;
    margin-bottom: 40px;
    margin-left: 30px;
  }
  
  #contact {
    font-family: "Inter", sans-serif;
    font-weight: 600;
    font-size: 18px;
    color: #ffffff;
    width: 250px;
    text-align: center;
   
  
  }
  
  strong {
    font-family: "Inter", sans-serif;
    font-size: 18px;
    font-weight: 600;
    color: #ffffff;
  }
  
  .contact-details {
    text-align: left;
    width: 500px;
    list-style: none;
    font-size: 18px;
    font-family:"Inter", sans-serif; 
    font-weight: 400;
    line-height: 1.2;
    
  
  }
  
  .footer-big {
    padding:0px;
   }
  
  .widget-about .contact-details {
    margin:30px 0 0 0;
   
  }
  .widget-about .contact-details li {
    margin-bottom:10px;
  
   }
  
   .widget-about .contact-details li span {
    padding-right:12px
   }
   .widget-about .contact-details li a {
    color:#ffffff;
  
   }
  
   .widget-about .contact-details li a:hover {
    text-decoration: underline;
    color: #E0007A;
  
    
   }
   @media (max-width:991px) {
    .footer-menu {
     padding-left: 0;
    }
   }
   
  .footer-widget {
   margin-bottom: 40px;
   display: flex;
   flex-direction: column;
   color: #ffffff;
  
     
  
  }
  
  .footer-widget a {
    margin-bottom: 12px;
    display: flex;
    flex-direction: column;
    color: #ffffff;
    line-height: 1.2;
    font-family: "Inter"sans-serif; 
   
   }
  
  .menuf {
    
    display: inline-block;
    list-style: none;
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    font-size: 20px;
    color: #ffffff;
  }
  
   .footer-menu ul li a {
    font-size: 18px;
    line-height: 22px;
    color: #ffffff;
    width: 200px;
    font-family:"Inter", sans-serif;
    -webkit-transition:.3s;
    -o-transition:.3s;
    transition:.3s
   }
   .footer-menu ul li a:hover {
    text-decoration: underline;
    color: #E0007A;
    list-style: none;
   }
  
   .footer-widget-middle a {
     color: #ffffff;
     font-family: "Inter", sans-serif; 
     font-size: 18px;
     font-weight: 600;
   }
  
   ul.social-network {
    list-style: none;
    display: inline;
    margin-left: 0 !important;
    padding: 0;
  }
  
  ul.social-network li {
    display: inline;
    margin: 0 5px;
  }
  
  
  .social-network a {
    background-color: #F9F9F9;
  }
  
  .image-icon {
    height: 50px;
    margin-right: 20px;
    margin-top: 40px;
   
  }
  
  .contact a{
    color: #ffffff;
    font-size: 18pxc;
    font-weight: 600;
  
  }
  