  }
  
  .about-list li {
    text-transform: capitalize;
    color: #000;
  }
  
  .progress {
    width: 100%;
    overflow: visible;
    position: relative;
    margin: 0 0 35px 0;
    border-radius: 0px;
    height: 7px;
    background-color: #f7f8f9;
  }
  
  .progress-bar {
    position: absolute;
    width: 0;
    top: 0;
    left: 0;
    height: 100%;
    background: #e1a34c;
    border-radius: 0px;
    -webkit-transition: all 3s ease-in-out;
    transition: all 3s ease-in-out;
  }
  

