button {
    padding: 16px;
    font-family: 'Inter', sans-serif;
    font-size: 16px;
    background-color: #E0007A;
    color: white;
    border-radius: 8px;
    cursor: pointer;
    margin-left: 100px;
  }

  #add-comment-form {
    border-bottom: 2px solid black;
    padding: 16px;
  }
  
  #add-comment-form h3 {
    margin-bottom: 16px;

  }
  
  #add-comment-form input, textarea {
    display: block;
    width: 300px;
    padding: 18px;
    border-radius: 8px;
    font-family: 'Quicksand', sans-serif;
    font-size: 18px;
    border: 1px solid black;
    margin-bottom: 16px;
    margin-left: 100px;
  }
  
  #add-comment-form button {
    width: 220px;
  }
  .comment {
    border-bottom: 1px solid black;
    padding: 16px;
   
  }

 .name-form {
     margin-left: 100px;
 }
