#title-home {
    font-family: "Inter", sans-serif;
    font-weight: 800;
    font-size: 200px;
    letter-spacing: -12px;
    margin-left: 70px;
  }

  .position-absolute {
    position: absolute;
  }
  
  .rw-words-1 span {
        top: 0;
        left: 0;
        opacity: 0;
        margin-left: 70px;
        letter-spacing: -10px;
        font-family: 'Inter', sans-serif;
        margin-top: -105px;
        color: #EA1581;
        font-size: 200px;
        font-weight: 800;
        -webkit-animation: rotateWord 8s linear infinite 0s;
        -ms-animation: rotateWord 8s linear infinite 0s;
        animation: rotateWord 8s linear infinite 0s;
      
      }
      .rw-words-1 span:nth-child(2) {
        -webkit-animation-delay: 2s;
        -ms-animation-delay: 2s;
        animation-delay: 2s;
        color: #707070;
     
      }
      .rw-words-1 span:nth-child(3) {
        -webkit-animation-delay: 4s;
        -ms-animation-delay: 4s;
        animation-delay: 4s;
        color: #31bcb9;
      
      }
      .rw-words-1 span:nth-child(4) {
        -webkit-animation-delay: 6s;
        -ms-animation-delay: 6s;
        animation-delay: 6s;
        color: #dce03a;
     
      }
      @-webkit-keyframes rotateWord {
        0% { opacity: 0; }
        2% { opacity: 0; -webkit-transform: translateY(-10px); }
        5% { opacity: 1; -webkit-transform: translateY(0px);}
        17% { opacity: 1; -webkit-transform: translateY(0px); }
        20% { opacity: 0; -webkit-transform: translateY(10px); }
        80% { opacity: 0; }
        100% { opacity: 0; }
      }
      @-ms-keyframes rotateWord {
        0% { opacity: 0; }
        2% { opacity: 0; -ms-transform: translateY(-10px); }
        5% { opacity: 1; -ms-transform: translateY(0px);}
        17% { opacity: 1; -ms-transform: translateY(0px); }
        20% { opacity: 0; -ms-transform: translateY(10px); }
        80% { opacity: 0; }
        100% { opacity: 0; }
      }
      @keyframes rotateWord {
        0% { opacity: 0; }
        2% { opacity: 0; -webkit-transform: translateY(-10px); transform: translateY(-10px); }
        5% { opacity: 1; -webkit-transform: translateY(0px); transform: translateY(0px);}
        17% { opacity: 1; -webkit-transform: translateY(0px); transform: translateY(0px); }
        20% { opacity: 0; -webkit-transform: translateY(10px); transform: translateY(10px); }
        80% { opacity: 0; }
        100% { opacity: 0; }
      }

      @media (max-width: 991px) {
        #title-home {
          font-size:100px;
          letter-spacing: -6px;
        }
        .rw-words-1 span {
          font-size: 100px;
          letter-spacing: -6px;
          margin-top: -55px;
        }
        .socialicons {
          margin-top: 100px;
        }
      }